import { request } from 'utils';

const prefix = '/api/categories';

export const getListRankTraining = () =>
  request({
    prefix,
    url: `/YearGrading`,
    method: 'GET',
  });

export const addListRankTraining = dataAddNew =>
  request({
    prefix,
    url: `/YearGrading`,
    data: dataAddNew,
    method: 'POST',
  });

export const getRankTrainingById = id =>
  request({
    prefix,
    url: `/YearGrading/${id}`,
    method: 'GET',
  });

export const editListRankTraining = RankTraining =>
  request({
    prefix,
    url: `/YearGrading/${RankTraining.id}`,
    data: RankTraining,
    method: 'PUT',
  });

export const deleteListRankTraining = id =>
  request({
    prefix,
    url: `/YearGrading/${id}`,
    method: 'DELETE',
  });



