import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import {
  addListRankTraining,
  getRankTrainingById,
  editListRankTraining,
} from 'services/RangkTraining';
import { Row, Card, Form, Input } from 'antd';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    graded: '',
    englishName: '',
    fromCredits: '',
    toCredits: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormRankLearning extends Component {
  state = {
    teacherId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ teacherId: match.params.id });
      this.getTeacherById(match.params.id);
    }
  };

  getTeacherById = async code => {
    const teacherMap = await getRankTrainingById(code);
    const initialValue = handleInitialValue(teacherMap.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    const { teacherId } = this.state;
    this.props.form.validateFields(async (err, values) => {
      if (teacherId) {
        await editListRankTraining(values);
        this.props.history.goBack();
      } else {
        await addListRankTraining(values);
        this.props.history.goBack();
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { teacherId } = this.state;
    return (
      <Card
        title={
          teacherId
            ? 'Cập nhật danh mục xếp hạng năm đào tạo'
            : 'Thêm danh mục xếp hạng năm đào tạo'
        }
      >
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item style={{ display: 'none' }} hasFeedback label="id" {...formItemLayout}>
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="Xếp loại" {...formItemLayout}>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên tiếng anh" hasFeedback>
              {getFieldDecorator('labelInEnglish', {
                initialValue: initialValue.labelInEnglish,
                rules: [
                  {
                    // required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ tín chỉ" hasFeedback>
              {getFieldDecorator('fromCredit', {
                initialValue: initialValue.fromCredit,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Đến tín chỉ" hasFeedback>
              {getFieldDecorator('toCredit', {
                initialValue: initialValue.toCredit,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel href="#/rank-training/list" />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormRankLearning {...props} />
  </Page>
));



